div[class^="page-"] {
    display: flex;
    flex-direction: column;
    justify-content: center;

    gap: 2em;

    width: 100%;
    height: 100%;

    box-shadow:inset 0px 0px 10px 6px var(--sublime-gray);
    background-color: var(--sublime-white);

    padding: 0;
    margin: 0;

    position: relative;
}

div.ui-bubble{
    display: flex;
    flex-direction: column;
    justify-content: center;

    padding: 1em 1em;
    margin: 0 auto;
    
    color: var(--sublime-white);
    background-color: var(--sublime-gray);
    box-shadow: 0.5em 0.5em 0.3em var(--ui-box-shadow);
    border: solid 0.1em var(--sublime-dark);
    border-radius: 1em;
    /*height: 60%;*/

    font-family: 'Courier New';
    font-size: 1em;
}

span.ui-title, .ui-bubble h2 {
    margin-bottom: 1em;
    font-weight: 700;
    color: var(--sublime-blue);
	font-size: 1.6em;
	text-align: center;
}

span.ui-subtitle, .ui-bubble h3 {
    margin-bottom: 0.4em;
    color: var(--sublime-orange);
	font-size: 1.4em;
}

span.ui-text, .ui-bubble h4 {
    margin-bottom: 0.2em;
    color: var(--sublime-yellow);
	font-size: 1.1em;
}

span.ui-alt-text, .ui-bubble p {
    margin-bottom: 0.1em;
    color: var(--sublime-white);
}

.ui-bubble span.ui-comment{
    margin-bottom: 0.1em;
    color: var(--sublime-dark);
}

.ui-bubble em,
span.ui-alt2-text {
    margin-bottom: 0;
    display: contents;
    color: var(--sublime-yellow);
}

.ui-bubble code {
    display: contents;
}

.ui-bubble ol li {
    color: var(--sublime-red);
}

div.ui-bubble a {
    display: contents;
    color: var(--sublime-yellow);
    font-style: italic;
    text-decoration: underline;
}

div.ui-button-wrapper {
    display: flex;
    margin: auto;
}

div.ui-button {
    display: flex;

    align-items: center;
    justify-content: center;

    font-family: 'Courier New';
    padding: 10px 20px;
    background-color: var(--sublime-gray); /* Bootstrap primary color */
    color: white;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.2s;
    margin: auto;
    padding: 2em;
}

#submit:hover {
    background-color: var(--sublime-very-dark); /* Darker shade on hover */
}

#submit:active {
    transform: scale(0.95); /* Slightly shrink on click */
}

/* Code syntax */

span.code-syntax-function {
    color: var(--sublime-blue);
    font-style: italic;
}

span.code-syntax-parameter {
    color: var(--sublime-orange);
    font-style: italic;
}

span.code-syntax-parenthesis {
    color: var(--sublime-red);  
    font-style: initial;
}

span.code-syntax-semicolon {
    color: var(--sublime-white);
}


@media only screen and (max-device-width: 480px){
    div[class^="page-"] {
        height: max-content;
        min-height: 100vh;
        padding-bottom: 2em;
        padding-top: 2em;

        /*overflow-y: scroll;*/
        /*overflow-x: hidden;*/
    }

    div.ui-bubble{
        font-size: 1em;
        width: 80%;
    }

    span.ui-title, .ui-bubble h2 {
        margin-bottom: 1em;
        font-weight: 700;
        color: var(--sublime-blue);
        font-size: 1.4em;
        text-align: center;
    }

    span.ui-subtitle, .ui-bubble h3 {
        margin-bottom: 0.4em;
        color: var(--sublime-orange);
        font-size: 1.2em;
    }

    span.ui-text, .ui-bubble h4 {
        margin-bottom: 0.2em;
        color: var(--sublime-yellow);
        font-size: 1em;
    }

}